body{
    margin: 0;
    background-color: #B9B9B9;
}
.container{
    display: grid;
    grid-template-columns: 15em auto;
    grid-template-areas: "nav main";
}
.nav{
    background-color: #B2ACAC;
    grid-area: nav;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;


}
.title{
    position: relative;
}
.title-top{
    position: absolute;
    top: -0.125em;
    left: 2em;
}
/* .title-main{} */

.about{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.projects{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.projectsall{
    max-width: 60vw;
    display: flex;
    overflow:  auto;

}
.highlights{
    max-width: 60vw;
    display: flex;
    overflow:  auto; 
}

.card{
    margin: 1em;

    background-color: white;
    color: rgb(0, 0, 0);
    display: grid;
    grid-template-columns: 2em 10em ;
    /* padding: 2px; */
    border: 0.2em solid lightblue;
    border-radius: 10px;
    width: 15em;
    grid-template-areas: "language title"
    "info info"
    "info info"
    ;

}

.card:hover{
    background-color: rgba(215, 230, 235, 0.685);
    cursor: pointer;
}

.c-language{

    grid-area: language;
}

.c-title{
    grid-area: title;
    margin-bottom: 0;
    }

.c-info{
    border-top: solid black 1px;
    grid-area: info;
    margin: 0;
    }